import "./style.css";
import { useEffect, useState } from "react";
import { connect } from "react-redux";
import socket from "../../services/socket";
import { connectSocket, joinRoomSocket } from "../../services/socket";
import { setListClientForMessenger } from "../../reducers/userReducer";
const Messenger = (props) => {
  const [userSelect, setUserSelect] = useState(null);
  useEffect(() => {
    connectSocket();
    props.setListClientForMessenger();
  }, []);
  const sendMessage = () => {
    //socket.emit();
  };
  const handleSelectUser = (user) => {
    setUserSelect(user);
  };
  return (
    <div className="messenger-container">
      <div className="row clearfix">
        <div className="col-lg-12">
          <div className="card chat-app">
            <div id="plist" className="people-list">
              <div className="input-group">
                <div className="input-group-prepend">
                  <span className="input-group-text">
                    <i className="fa fa-search"></i>
                  </span>
                </div>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Search..."
                />
              </div>
              <ul className="list-unstyled chat-list mt-2 mb-0">
                {props.users.map((user) => (
                  <li
                    key={user._id}
                    className={
                      userSelect && user._id === userSelect._id
                        ? `clearfix active`
                        : `clearfix`
                    }
                    onClick={(e) => handleSelectUser(user)}
                  >
                    <img
                      src="https://bootdey.com/img/Content/avatar/avatar3.png"
                      alt="avatar"
                    />
                    {(userSelect && user._id === userSelect._id) ||
                    user.recentMessage.isAdminSending ? (
                      <div className="about">
                        <div className="name">{user.deviceId}</div>
                        <div className="recent-message">
                          <span>{user.recentMessage.content}</span>
                          <span></span>
                        </div>
                      </div>
                    ) : (
                      <div className="about">
                        <div className="name-client-sending">
                          {user.deviceId}
                        </div>
                        <div className="recent-message-client-sending">
                          <span>{user.recentMessage.content}</span>
                        </div>
                      </div>
                    )}
                  </li>
                ))}
              </ul>
            </div>
            <div className="chat">
              <div className="chat-header clearfix">
                <div className="row">
                  <div className="col-lg-6">
                    <img
                      src="https://bootdey.com/img/Content/avatar/avatar2.png"
                      alt="avatar"
                    />
                    <div className="chat-about">
                      <h6 className="m-b-0">Aiden Chavez</h6>
                      <small>Last seen: 2 hours ago</small>
                    </div>
                  </div>
                </div>
              </div>
              <div className="chat-history">
                <ul className="m-b-0">
                  <li className="clearfix">
                    <div className="message-data text-right">
                      <span className="message-data-time">10:10 AM, Today</span>
                      <img
                        src="https://bootdey.com/img/Content/avatar/avatar7.png"
                        alt="avatar"
                      />
                    </div>
                    <div className="message other-message float-right">
                      {" "}
                      Hi Aiden, how are you? How is the project coming along?{" "}
                    </div>
                  </li>
                  <li className="clearfix">
                    <div className="message-data">
                      <span className="message-data-time">10:12 AM, Today</span>
                    </div>
                    <div className="message my-message">
                      Are we meeting today?
                    </div>
                  </li>
                  <li className="clearfix">
                    <div className="message-data">
                      <span className="message-data-time">10:15 AM, Today</span>
                    </div>
                    <div className="message my-message">
                      Project has been already finished and I have results to
                      show you.
                    </div>
                  </li>
                </ul>
              </div>
              <div className="chat-message clearfix">
                <div className="input-group mb-0">
                  <div className="input-group-prepend" onClick={sendMessage}>
                    <span className="input-group-text">
                      <i className="fa fa-send"></i>
                    </span>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter text here..."
                  />
                  <div className="input-images">
                  <i className="fa-solid fa-image"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    users: state.users,
    messages: state.messages,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setListClientForMessenger: () => dispatch(setListClientForMessenger()),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Messenger);
