import { createSlice } from "@reduxjs/toolkit";
import messageService from "../services/message";

const messageSlice = createSlice({
  name: "message",
  initialState: [],
  reducers: {
    setMessages: (state, action) => {
      return action.payload;
    },
    appendMessage: (state, action) => {
      return state.concat(action.payload);
    },
  },
});

export const setMessagesByDeviceId = (deviceId) => {
  return async(dispatch) => {
    const messages = await messageService.getMessageByDeviceId(deviceId);
    dispatch(messageSlice.actions.setMessages(messages));
  };
};

export const executedConvertationReceiveMessage = (data) => {
  return async(dispatch) => {
    const message = await messageService.sendMessageFromAdmin(data);
    dispatch(messageSlice.actions.appendMessage(message));
  };
};

export default messageSlice.reducer;
