import { createSlice } from "@reduxjs/toolkit";
import userService from "../services/user";

const userSlice = createSlice({
  name: "user",
  initialState: [],
  reducers: {
    setUsers: (state, action) => {
      return action.payload;
    },
    setUsersAfterAdminSendMessage: (state, action) => {
      for (let i = 0; i < state.length; i++) {
        if (state[i].deviceId === action.payload.deviceId) {
          state[i].recentMessage = {
            content: action.payload.content,
            images: action.payload.images,
            createdAt: new Date(),
            isAdminSending: action.payload.isAdminSending,
          };
          return state;
        }
      }
    },
    appendUser: (state, action) => {
      return [action.payload].concat(state);
    },
  },
});

export const setListClientForMessenger = () => {
  return async (dispatch) => {
    const users = await userService.getListClientForMessenger();
    dispatch(userSlice.actions.setUsers(users));
  };
};

export const executedUserReceiveMessage = (data, users) => {
  return async (dispatch) => {
    if (data.isAdminSending) {
      dispatch(userSlice.actions.setUsersAfterAdminSendMessage(data));
    } else {
      for (let i = 0; i < users.length; i++)
        if (users[i].deviceId === data.deviceId) return;
      const client = await userService.getClientByDeviceId(data.deviceId);
      dispatch(userService.appendUser(client));
    }
  };
};

export default userSlice.reducer;